import React from 'react';
import styles from './Works.module.css'
import WorksItem from "./WorksItem/WorksItem";
import Title from "../Title/Title";
import counterImg from "../../image/2.png";
import walletImg from "../../image/3.png"

const Works = (props) => {

    const backCount =  {
        backgroundImage: `url(${counterImg})`
    };
    const backWallet =  {
        backgroundImage: `url(${walletImg})`
    }


    return (
        <div className={styles.works}>
            <div className={styles.container}>
               <Title name={props.name}/>
                <div className={styles.works__items}>
                    <WorksItem name={props.buttonName} style={backCount}/>
                    <WorksItem name={props.buttonName} style={backWallet}/>
                </div>
            </div>
        </div>
    );
}

export default Works;
